@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap'); */
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.flex {
  display: flex;
}

.card__container {
  align-items: center;
  flex-direction: column;
  margin: auto;
  margin-bottom: 30px;
  border-bottom:darkgray solid 1px;
  padding: 5px;
  width: 95%;
}

.card__content {
  align-items: flex-start;
  flex-direction: column;
  justify-content: space-between;
  margin: 02px 10px;
}

.card__img {
  width: 100%;

}

.card__title {
  font-size: 25px;
  text-align: left;
}

.card__author__container {
  margin-top: 5px;

  text-align: left;
}

.card__author__by {
  color: #565656;
}

.card__author__name {
  color: #565656;
  margin-left: 5px;
}

@media screen and (min-width: 768px) {
  .card__container {
    width: 80%;
  }

  .card__content {
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;
    margin: 02px 10px;
    height: 130px;
  }

  .card__container {
    flex-direction: row;
    border-bottom: none;
  }

  .card__img {
    border-radius: 20px;
    width: 300px;
  
  }
  .card__title {
    font-size: 30px;
    text-align: left;
  }

  .card__author__container {
    font-size: 25px;
  }
}

.flex {
  display: flex;
}
.loader__container {
  justify-content: center;
  align-items: center;
 
}
.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.flex {
  display: flex;
}

.header {
  align-items: center;
  background-color: black;
  height: 60px;
  justify-content: center;
}
.header__logo_container {
  align-items: center;
  justify-content: center;
}

.header__logo-img {
}

@media screen and (min-width: 768px) {
  .header {
    background-color: black;
  }
}

.flex {
  display: flex;
}

.home__section {
  margin: 0px 0px;
}

.home__section button {
  background: #fff;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  font-family: Roboto, sans-serif;
  height: 34px;
  margin: 0px 8px;
  text-align: center;
}

.home__container {
  margin: 5px 2px;
}

.home__container__pages {
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  justify-content: center;
}

.home__container__pages p {
}

@media screen and (min-width: 768px) {
  .home__container {
    margin: 20px 2px;
  }

  .home__container__pages {
    font-size: 25px;
  }

  .home__section button {
    font-size: 25px;
    height: 54px;
    margin: 0px 18px;
    width: 220px;
  }
}

.flex {
  display: flex;
}

.post__section {
  margin: 0px 8px;
  justify-content: center;
}

.post__container {
  margin: 5px 2px;
  padding: 0px 15px;
  align-items: center;
  flex-direction: column;
}

.post__container img {
  width: 80%;
  height: auto;
}

.post__container__article {
  align-items: center;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  width: 95%;
}

.post__container__article table {
  text-align: center;
  background:#fafafa ;
  margin: 0 auto;
  width: 80%;
}

.post__container__article tr:nth-child(even) {
  background:lightgray;
}

.post__container__article p {
  font-size: 20px;
}

.post__container__article a {
  text-decoration: none;
}

.post__container__article img {
  width: 100%;
  height: auto;
}

.post__container__article h2 {
}
@media screen and (min-width: 768px) {
  .post__container {
    margin: 20px 2px;
    width: 80%;
  }

  .post__container h1 {
    font-size: 60px;
  }
  .post__container__article p {
    font-size: 30px;
  }

  .post__container img {
    width: 100%;
    height: auto;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

